"use client";

import { useState } from "react";
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer } from "recharts";

export default function EVOptimizer() {
  const [battery, setBattery] = useState(50);
  const [power, setPower] = useState(7);
  const [departureTime, setDepartureTime] = useState("");
  const [cheapMode, setCheapMode] = useState(false);

  const [recommendation, setRecommendation] = useState<any>(null);
  const [chartData, setChartData] = useState<any[]>([]);
  const [countdown, setCountdown] = useState<string>("");

  async function getRecommendation() {
    if (!departureTime) {
      alert("Please select departure time");
      return;
    }

    const now = new Date();
    const target = new Date();
    const [h, m] = departureTime.split(":").map(Number);
    target.setHours(h);
    target.setMinutes(m);

    // Calculate Countdown
    const diffMs = target.getTime() - now.getTime();
    if (diffMs <= 0) {
      alert("Departure time must be in the future!");
      return;
    }

    const hoursLeft = Math.floor(diffMs / 3600000);
    const minsLeft = Math.floor((diffMs % 3600000) / 60000);
    setCountdown(`${hoursLeft}h ${minsLeft}m`);

    const res = await fetch("http://127.0.0.1:8000/predict", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        battery_level: battery,
        target_time: hoursLeft, // Send hours left
        cheap_mode: cheapMode,
        power: power
      }),
    });

    const data = await res.json();
    setRecommendation(data);

    // Mock SOC growth chart (You replace with real FastAPI prediction later)
    const points: any[] = [];
    let soc = battery;
    for (let i = 0; i <= hoursLeft; i++) {
      soc = Math.min(100, soc + power * 2); // Just sample growth
      points.push({ time: `+${i}h`, soc });
    }
    setChartData(points);
  }

  return (
    <div className="min-h-screen bg-gray-950 text-white p-6 flex flex-col items-center">
      <div className="max-w-xl w-full space-y-6">
        <h1 className="text-3xl font-bold text-center">
          ‚ö° Smart EV Charging Optimizer
        </h1>

        {/* Battery Level */}
        <div>
          <label className="block mb-2 font-semibold text-gray-300">
            Current Battery Level: {battery}%
          </label>
          <input
            type="range"
            min="0"
            max="100"
            value={battery}
            onChange={(e) => setBattery(Number(e.target.value))}
            className="w-full accent-blue-500"
          />
        </div>

        {/* Power */}
        <div>
          <label className="block mb-2 font-semibold text-gray-300">
            Charger Power (kW)
          </label>
          <select
            value={power}
            onChange={(e) => setPower(Number(e.target.value))}
            className="w-full p-2 rounded bg-gray-800 border border-gray-700"
          >
            <option value={3}>3 kW</option>
            <option value={7}>7 kW</option>
            <option value={11}>11 kW</option>
          </select>
        </div>

        {/* Departure time */}
        <div>
          <label className="block mb-2 font-semibold text-gray-300">
            Departure Time
          </label>
          <input
            type="time"
            value={departureTime}
            onChange={(e) => setDepartureTime(e.target.value)}
            className="w-full p-2 rounded bg-gray-800 border border-gray-700"
          />
        </div>

        {/* Cheap electricity option */}
        <label className="flex items-center gap-2">
          <input
            type="checkbox"
            checked={cheapMode}
            onChange={() => setCheapMode(!cheapMode)}
          />
          Optimize for low electricity cost üí∏
        </label>

        {/* Button */}
        <button
          onClick={getRecommendation}
          className="w-full py-3 font-bold rounded bg-blue-600 hover:bg-blue-700 transition"
        >
          Optimize Charging üî•
        </button>

        {/* Results Section */}
        {recommendation && (
          <div className="bg-gray-800 p-4 rounded-lg border border-gray-700 space-y-3">
            <p className="font-bold text-lg text-green-400">Recommendation:</p>
            <p>‚è± Countdown: {countdown}</p>
            <p>üöó Suggest Charging: {recommendation.hours} hours</p>
            <p>üîã Final Battery: {recommendation.final_battery}%</p>
          </div>
        )}

        {/* Chart */}
        {chartData.length > 0 && (
          <div className="h-60 bg-gray-900 p-3 rounded-lg border border-gray-700">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={chartData}>
                <XAxis dataKey="time" />
                <YAxis domain={[0, 100]} />
                <Tooltip />
                <Line type="monotone" dataKey="soc" stroke="#00d2ff" strokeWidth={2} />
              </LineChart>
            </ResponsiveContainer>
          </div>
        )}
      </div>
    </div>
  );
}
